
class Logger {
	
	private groups: string[] = [];

	group(name: string) {
		this.groups.push(name)
		console.group(name);
	}

	error(error: any) {
		console.error(error);
	}

	log(...args: any[]) {
		console.log(...args);
	}

	ungroup() {
		this.groups.pop();
		console.groupEnd();
	}
}

export const logger = new Logger();


