export interface Results<T> {
	records: T[];
	count: number;
	total: number;
	pages: number;
	page: {
		index: number;
		size: number;
		first: boolean;
		last: boolean;
	};
}

export type YesOrNo = "Y" | "N" | '';

export interface Tasks {
	id?: string,
	process?: string,
	customer: {
		name?: string, // var.FIRST_NAME
		surname?: string, // var.LAST_NAME
		address?: string, // var.ADDRESS
		state?: string, // var.STATE
		zip?: string, // var.ZIP
		phone?: string, // var.PHONE
		email?: string, // var.EMAIL
		pan?: string, // var.PAN
		accountId?: string, // var.CMS_ACCOUNT_ID
		foundAccountId?: string // var.FOUND_ACCOUNT_ID
	},
	ticket: {
		id?: string, // var.TICKET_ID
		createdByName?: string, // var.CREATED_BY_NAME
		createdByUsername?: string, // var.CREATED_BY_USERNAME
		departament?: string, // var.RESOLVING_DEPARTMENT
		source?: string // var.TICKET_SOURCE
	},
	complaint: {
		id?: string, // var.FD_COMPLAINT_ID
		source?: string, // var.COMPLAINT_SOURCE
		origin?: string, // var.DEPARTMENT_ORIGINATED
		type?: string, // var.COMPLAINT_TYPE
		dateReceived?: Date, // var.COMPLAINT_RECEIVED_DATE
		escalated?: YesOrNo, // var.BCO_LEGAL_ESCALATED
		level?: string, // var.LEVEL
		summary?: string, // var.SUMMARY_OF_FACTS
		category: {
			main?: string, // var.COMPLAINT_CATEGORY
			second?: string, // var.COMPLAINT_SUBCATEGORY
			other?: string, // var.COMPLAINT_OTHER_CATEGORY
			hcus: {
				main?: string, // var.HCUS_COMPLAINT_CATEGORY
				second?: string // var.HCUS_COMPLAINT_SUBCATEGORY
			}
		},
		causedByFD?: YesOrNo, // var.CAUSED_BY_FD
		methodReceived?: string, // var.METHOD_RECEIVED
		desiredResolution?: string, // var.DESIRED_RESOLUTION
		sla: {
			internal?: string, // var.SLA_INTERNAL
			overall?: string // var.SLA_OVERALL
		},
		cimsNumber?: string; // var.CIMS_NUMBER
	},
	resolution?: TaskResolution,
	original?: BOSSTaks;
	files?: TaskAttachements[];
}

export interface TaskResolution {
	// Investigations Findings	Optional	Text	<Multiple lines of text>	No max. Limit
	findings?: string; // var.FINDINGS
	// Monetary Relief Amount	Optional	Currency	25.75	2 Decimal Places
	// Monetary Relief (Other Non-Currency)	Optional	Text	<Single Line of text>	225 char max.
	monetaryRelief?: {
		amount?: string; // var.MONETARY_RELIEF_AMOUNT
		other?: string; // var.MONETARY_RELIEF_OTHER
	};
	// Response Method	Optional	Set	, "Phone", "Fax", "Email", "Other"	
	// Response Rejected	Optional	Set	YES or NO	N/A
	response?: {
		method?: string; // var.RESPONSE_METHOD
		rejected?: YesOrNo; // var.RESPONSE_REJECTED
	};
	// Conclusion	Optional	"Facts support allegation", "Facts do not support allegation", "Not enough facts to support allegation", "N/A	", 
	conclusion?: string; // var.CONCLUSION
	// Status	Mandatory	Set	"Pending Client", "Closed", "Closed  consumer was unresponsive", "Closed with explanation  educational opportunity", "Closed with monetary relief adjustments, rewards, etc", "Closed with non-monetary relief", "Closed with non-monetary relief  complainant did not want to be contacted further", "Respond to customer - 1st attempt", "Respond to customer - 2nd attempt", "Respond to customer - 3rd attempt", "Respond to customer - 4th attempt", "Requires escalation", "Reopened"
	status?: string; // var.STATUS
	// Customer Contact Successful Mandatory	Set	"Successful", "Unsuccessful no contact", "Unsuccessful bad number"
	customerContactSuccessful?: string; // var.CUSTOMER_CONTACT_SUCCESSFUL
	// Letter Sent	Optional	Set	"YES" "NO"	N/A
	// Date Letter Sent	Optional	Time Stamp (YYYY-MM-DD)	2017-11-31	N/A
	letter?: {
		sent?: YesOrNo; // var.LETTER_SENT
		date?: Date; // var.LETTER_SENT_DATE
	};
	// Resolved in Interaction?	Optional	Set	YES/NO	N/A
	resolvedInInteraction?: YesOrNo; // var.RESOLVED_IN_INTERACTION
	// Outbound Call Attempts	Optional 	"1", "2", "3", "4"	
	// First Outbound Call Placed	Optional	Time Stamp (YYYY-MM-DD)	2017-11-31	N/A
	// Second Outbound Call Placed	Optional	Time Stamp (YYYY-MM-DD)	2017-11-31	N/A
	// Third Outbound Call Placed	Optional	Time Stamp (YYYY-MM-DD)	2017-11-31	N/A
	// Fourth Outbound Call Placed	Optional	Time Stamp (YYYY-MM-DD)	2017-11-31	N/A
	calls?: {
		attempts?: string; // var.CALL_ATTEMPTS_COUNT
		first?: Date; // var.CALL_1_DATE
		second?: Date; // var.CALL_2_DATE
		third?: Date; // var.CALL_3_DATE
		fourth?: Date; // var.CALL_4_DATE
	}
	cimsClosedDate?: Date, // var.CIMS_CLOSED_DATE
	// Submission Date	Optional	Time Stamp (YYYY-MM-DD)	2017-11-31	N/A
	submissionDate?: Date; // var.SUBMISSION_DATE
}

