import * as React from 'react';

import Dialog from 'react-toolbox/lib/dialog';
import { Snackbar } from 'react-toolbox/lib/snackbar';
import Input from 'react-toolbox/lib/input';
import {Button, IconButton} from 'react-toolbox/lib/button';
import {Dropdown} from 'react-toolbox/lib/dropdown';
import Checkbox from 'react-toolbox/lib/checkbox';
import { BrowserRouter as Router, Route, Link, RouteComponentProps } from 'react-router-dom';
import { format } from 'date-fns';

var ReactPaginate = require('react-paginate');

import {
    FormattedMessage,
    FormattedNumber,
    FormattedRelative,
    InjectedIntlProps,
    injectIntl
} from 'react-intl';

import { Table, TableHead, TableRow, TableCell } from 'react-toolbox/lib/table';
import { dataService } from '../data-service';
import { logger } from '../logger';

import { Tasks, Results, BOSSUser, TaskSchemaDefinition, TaskSchemaProperty, TaskSchemaPropertyOptions, YesOrNo} from '../models';

import * as PubSub from 'pubsub-js';

import { withRouter } from 'react-router-dom';
import FontIcon from 'react-toolbox/lib/font_icon';

interface HomeComponentProps extends RouteComponentProps<any>, InjectedIntlProps {

}

interface HomeComponentState {
	results?: Results<Tasks>;
}

export class HomeComponentWrapped extends React.Component<HomeComponentProps, HomeComponentState> {

	loadTasksTimer: number;

	state: HomeComponentState = {
	};

	constructor(props: HomeComponentProps) {
		super(props);
	}

	componentDidMount() {
		this.loadTasks(0);
	}

	loadTasks(page: number = 0) {
		if (this.state.results && this.state.results.page.index == page) {
			return;
		}
		logger.group('Loading inbox');
		logger.log('Loading inbox page: ', page);
		dataService.loadTasks(page, 'inbox').then((results: Results<Tasks>)=> {
			logger.log('Loaded inbox page: ', results);
			this.setState({results})
			logger.ungroup();
		}).catch((error)=> {
			logger.error(error);
            PubSub.publish('ERROR', `Could not load list of complaint tickets`);
            logger.ungroup();
        });
	}

	intl(property: TaskSchemaProperty, value: string): string {
		if (!value) {
			return " - ";
		}
		if (!property.options) {
			return value;
		}
		var option: TaskSchemaPropertyOptions = property.options.find((option: TaskSchemaPropertyOptions)=> {
            return option.value == value;
        });
        if (!option) {
			return value;
        }
        return this.props.intl.formatMessage({id: option.key});
	}

	render() {
		var user: BOSSUser = dataService.getUser();
		var {results} = this.state;
		if (!results) {
			return <div></div>;
		}
		var {schema} = dataService;
		return (
			<div className="col-xs-12">
				<div className="row">
					<div className="col-xs-12">
						<div className="row">
							<div className="tableContainer">
								<div className="app-content__table">
									<Table selectable={false} style={{ marginTop: 10 }}>
										<TableHead>
											<TableCell>
												<FormattedMessage id="NON EXISTING ID" defaultMessage="Ticket" />
											</TableCell>
											<TableCell>
												<FormattedMessage id="NON EXISTING ID" defaultMessage="Ticket Creation Time" />
											</TableCell>
											<TableCell>
												<FormattedMessage id="NON EXISTING ID" defaultMessage="Complaint Category" />
											</TableCell>
											<TableCell>
												<FormattedMessage id="NON EXISTING ID" defaultMessage="Complaint Subcategory" />
											</TableCell>
											<TableCell>
												<FormattedMessage id="NON EXISTING ID" defaultMessage="Ticket Level" />
											</TableCell>
											<TableCell>
												<FormattedMessage id="NON EXISTING ID" defaultMessage="SLA" />
											</TableCell>
											<TableCell>
												<FormattedMessage id="NON EXISTING ID" defaultMessage="Assignee" />
											</TableCell>
											<TableCell>
												<FormattedMessage id="NON EXISTING ID" defaultMessage="Ticket source" />
											</TableCell>
										</TableHead>

										{
											results && results.total ?
											results.records.map((task: Tasks)=> (
												<TableRow key={task.id} onDoubleClick={()=> this.props.history.push(`/complaint/${task.process}/${task.id}`)}>
													<TableCell>{task.ticket.id}</TableCell>
													<TableCell>{task.resolution.submissionDate ? format(task.resolution.submissionDate, 'MM/DD/YYYY') : ' - '}</TableCell>
													<TableCell>{this.intl(schema.COMPLAINT_CATEGORY, task.complaint.category.main)}</TableCell>
													<TableCell>{this.intl(schema.COMPLAINT_SUBCATEGORY, task.complaint.category.second)}</TableCell>
													<TableCell>{this.intl(schema.LEVEL, task.complaint.level)}</TableCell>
													<TableCell>{task.complaint.sla ? format(new Date(task.complaint.sla as string), 'MM/DD/YYYY') : ' - '}</TableCell>
													<TableCell>{task.original.assignee || ' - '}</TableCell>
													<TableCell>{this.intl(schema.TICKET_SOURCE, task.ticket.source)}</TableCell>
												</TableRow>
											))
											: null
										}

									</Table>
								</div>

								{
									results && results.pages > 1 ?
									<div className="col-xs-12" id="react-paginate">
										<ReactPaginate previousLabel={ <FormattedMessage id="button.prev" /> }
											nextLabel={ <FormattedMessage id="button.next" /> }
											initialPage={0}
											breakLabel={<span>...</span>}
											pageCount={results.pages}
											marginPagesDisplayed={2}
											pageRangeDisplayed={5}
											onPageChange={ (page: any) => this.loadTasks(page.selected) }
											containerClassName={"pagination"}
											activeClassName={"active"}
											pageClassName={'page-item'} />
									</div>
									: null
								}

							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}


export const HomeComponent = injectIntl(HomeComponentWrapped);


