var axiosInstance: AxiosInstance;

// https://sso.cn74a7.cz.infra/opensso/UI/Login?goto= China SSO
// https://sso.ussso0.cz.nonprod/opensso/UI/Login?goto= US SSO


function ssoInterceptor(error: AxiosError) {
	if (error && error.response && +error.response.status == 401) {
		// X-Authenticate:https://sso.ussso0.cz.nonprod/opensso/UI/Login
		// X-Authentication:openam
		var headers: any = error.response.headers;
		var isOpenAM: boolean = (headers['X-Authentication'] || headers['x-authentication']) == 'openam';
		var ssoURL: string = headers['X-Authenticate'] || headers['x-authenticate'];
		if (isOpenAM && ssoURL) {
			dataService.config.url.sso = ssoURL;
			// For testing purposes: It doesn't work.
			// ssoURL = `https://sso.cn74a7.cz.infra/opensso/UI/Login`;
			// Redirect to the SSO address. When returning, everything will be ok!
			window.location.href = `${ssoURL}?goto=${document.location.href}`;
		}
	}
	return Promise.reject(error);
}

export interface ValidationInfo {
	[key: string]: string,
	hasErrors: "true" | "false"
}

export interface DataService {
	config: BOSSConfig;
	rights: BOSSUserRights;
	schema: TaskSchemaDefinition;
	getUser: () => BOSSUser;
	setConfig: (config: BOSSConfig) => void;
	setUserRights: (userRights: BOSSUserRights) => void;
	logout: () => void;
	loadUser: ()=> Promise<BOSSUser>;
	loadLanguage: (lang: string)=> Promise<Map<string, string>>;
	loadLanguages: ()=> Promise<Array<BOSSLanguages>>;

	loadSchemaDefinition: ()=> Promise<TaskSchemaDefinition>;
	
	newTask() : Tasks;
	loadTasks(page: number, type?: string, filters?: {name: string, value: string}[]): Promise<Results<Tasks>>;
	loadTask: (pid: string, id: string)=> Promise<Tasks>;
	createTask: (t: Tasks)=> Promise<Tasks>;
	updateTask: (t: Tasks)=> Promise<Tasks>;
	resolveTask: (t: Tasks)=> Promise<Tasks>;
	validateTask: (task: Tasks)=> ValidationInfo;
	
	getAllowedUsers: (task: Tasks)=> Promise<BOSSUsers[]>;
	assignTask(task: Tasks, user: string): Promise<boolean>;
	unassignTask(task: Tasks): Promise<boolean>;
	claimTask(task: Tasks): Promise<Tasks>;

}

function processTask(t: BOSSTaks) : Tasks {
	var task: Tasks = {
		id: t.id,
		process: t.processInstanceId,
		customer: {
			name: isEmptyString(t.variables.FIRST_NAME),
			surname: isEmptyString(t.variables.LAST_NAME),
			address: isEmptyString(t.variables.ADDRESS),
			state: isEmptyString(t.variables.STATE),
			zip: isEmptyString(t.variables.ZIP),
			phone: isEmptyString(t.variables.PHONE),
			email: isEmptyString(t.variables.EMAIL),
			pan: isEmptyString(t.variables.PAN),
			foundAccountId: isEmptyString(t.variables.FOUND_ACCOUNT_ID),
			accountId: isEmptyString(t.variables.CMS_ACCOUNT_ID)
		},
		ticket: {
			id: isEmptyString(t.variables.TICKET_ID),
			createdByName: isEmptyString(t.variables.CREATED_BY_NAME),
			createdByUsername: isEmptyString(t.variables.CREATED_BY_USERNAME),
			departament: isEmptyString(t.variables.RESOLVING_DEPARTMENT),
			source: isEmptyString(t.variables.TICKET_SOURCE)
		},
		complaint: {
			id: isEmptyString(t.variables.FD_COMPLAINT_ID),
			source: isEmptyString(t.variables.COMPLAINT_SOURCE),
			origin: isEmptyString(t.variables.DEPARTMENT_ORIGINATED),
			type: isEmptyString(t.variables.COMPLAINT_TYPE),
			dateReceived: isEmptyDate(t.variables.COMPLAINT_RECEIVED_DATE),
			escalated: isEmptyBoolean(t.variables.BCO_LEGAL_ESCALATED),
			level: isEmptyString(t.variables.LEVEL),
			summary: isEmptyString(t.variables.SUMMARY_OF_FACTS),
			category: {
				main: isEmptyString(t.variables.COMPLAINT_CATEGORY),
				second: isEmptyString(t.variables.COMPLAINT_SUBCATEGORY),
				other: isEmptyString(t.variables.COMPLAINT_OTHER_CATEGORY),
				hcus: {
					main: isEmptyString(t.variables.HCUS_COMPLAINT_CATEGORY),
					second: isEmptyString(t.variables.HCUS_COMPLAINT_SUBCATEGORY)	
				}
			},
			causedByFD: isEmptyBoolean(t.variables.CAUSED_BY_FD),
			methodReceived: isEmptyString(t.variables.METHOD_RECEIVED),
			desiredResolution: isEmptyString(t.variables.DESIRED_RESOLUTION),
			sla: {
				internal: isEmptyString(t.variables.SLA_INTERNAL),
				overall: isEmptyString(t.variables.SLA_OVERALL)
			},
			cimsNumber: isEmptyString(t.variables.CIMS_NUMBER)
		},
		resolution: {
			findings: isEmptyString(t.variables.FINDINGS),
			monetaryRelief: {
				amount: isEmptyString(t.variables.MONETARY_RELIEF_AMOUNT),
				other: isEmptyString(t.variables.MONETARY_RELIEF_OTHER)
			},
			response: {
				method: isEmptyString(t.variables.RESPONSE_METHOD),
				rejected: isEmptyBoolean(t.variables.RESPONSE_REJECTED),
			},
			conclusion: isEmptyString(t.variables.CONCLUSION),
			status: isEmptyString(t.variables.STATUS),
			customerContactSuccessful: isEmptyString(t.variables.CUSTOMER_CONTACT_SUCCESSFUL),
			letter: {
				sent: isEmptyBoolean(t.variables.LETTER_SENT),
				date: isEmptyDate(t.variables.LETTER_SENT_DATE),
			},
			resolvedInInteraction: isEmptyBoolean(t.variables.RESOLVED_IN_INTERACTION),
			calls: {
				attempts: isEmptyString(t.variables.CALL_ATTEMPTS_COUNT),
				first: isEmptyDate(t.variables.CALL_1_DATE),
				second: isEmptyDate(t.variables.CALL_2_DATE),
				third: isEmptyDate(t.variables.CALL_3_DATE),
				fourth: isEmptyDate(t.variables.CALL_4_DATE),
			},
			cimsClosedDate: isEmptyDate(t.variables.CIMS_CLOSED_DATE),
			submissionDate: isEmptyDate(t.variables.SUBMISSION_DATE)
		}
	};

	task.original = t;

	return task;
}

function unprocessTask(task: Tasks): BOSSTaksVariables {
	var t: BOSSTaksVariables = {
		FIRST_NAME: task.customer.name,
		LAST_NAME: task.customer.surname,
		ADDRESS: task.customer.address,
		STATE: task.customer.state,
		ZIP: task.customer.zip,
		PHONE: task.customer.phone,
		EMAIL: task.customer.email,
		PAN: task.customer.pan,
		CMS_ACCOUNT_ID: task.customer.accountId,

		TICKET_ID: task.ticket.id,
		CREATED_BY_NAME: task.ticket.createdByName,
		CREATED_BY_USERNAME: task.ticket.createdByUsername,
		RESOLVING_DEPARTMENT: task.ticket.departament,
		TICKET_SOURCE: task.ticket.source,

		FD_COMPLAINT_ID: task.complaint.id,
		COMPLAINT_SOURCE: task.complaint.source,
		DEPARTMENT_ORIGINATED: task.complaint.origin,
		COMPLAINT_TYPE: task.complaint.type,
		COMPLAINT_RECEIVED_DATE: task.complaint.dateReceived ? format(task.complaint.dateReceived, 'YYYY-MM-DD'): "",
		BCO_LEGAL_ESCALATED: task.complaint.escalated,
		LEVEL: task.complaint.level,
		SUMMARY_OF_FACTS: task.complaint.summary,

			COMPLAINT_CATEGORY: task.complaint.category.main,
			COMPLAINT_SUBCATEGORY: task.complaint.category.second,
			COMPLAINT_OTHER_CATEGORY: task.complaint.category.other,

				HCUS_COMPLAINT_CATEGORY: task.complaint.category.hcus.main,
				HCUS_COMPLAINT_SUBCATEGORY: task.complaint.category.hcus.second,

		CAUSED_BY_FD: task.complaint.causedByFD,
		METHOD_RECEIVED: task.complaint.methodReceived,
		DESIRED_RESOLUTION: task.complaint.desiredResolution,

			// SLA_INTERNAL: task.complaint.sla.internal,
			// SLA_OVERALL: task.complaint.sla.overall,


		CIMS_NUMBER: task.complaint.cimsNumber
	};

	var resolution: BOSSTaksVariables = {

		FINDINGS: task.resolution.findings,
		
			MONETARY_RELIEF_AMOUNT: task.resolution.monetaryRelief.amount,
			MONETARY_RELIEF_OTHER: task.resolution.monetaryRelief.other,
			
			RESPONSE_METHOD: task.resolution.response.method,
			RESPONSE_REJECTED: task.resolution.response.rejected,
		
		CONCLUSION: task.resolution.conclusion,
		STATUS: task.resolution.status,
		CUSTOMER_CONTACT_SUCCESSFUL: task.resolution.customerContactSuccessful,
		
			LETTER_SENT: task.resolution.letter.sent,
			LETTER_SENT_DATE: task.resolution.letter.date ? format(task.resolution.letter.date, 'YYYY-MM-DD'): "",
		
		RESOLVED_IN_INTERACTION: task.resolution.resolvedInInteraction,
		
			CALL_ATTEMPTS_COUNT: task.resolution.calls.attempts,
			CALL_1_DATE: task.resolution.calls.first ? format(task.resolution.calls.first, 'YYYY-MM-DD'): "",
			CALL_2_DATE: task.resolution.calls.second ? format(task.resolution.calls.second, 'YYYY-MM-DD'): "",
			CALL_3_DATE: task.resolution.calls.third ? format(task.resolution.calls.third, 'YYYY-MM-DD'): "",
			CALL_4_DATE: task.resolution.calls.fourth ? format(task.resolution.calls.fourth, 'YYYY-MM-DD'): "",
		
		CIMS_CLOSED_DATE: task.resolution.cimsClosedDate ? format(task.resolution.cimsClosedDate, 'YYYY-MM-DD'): "",
		SUBMISSION_DATE: task.resolution.submissionDate ? format(task.resolution.submissionDate, 'YYYY-MM-DD'): "",
		
	};

	if (task.id) {
		t = Object.assign({}, t, resolution);
	}
	Object.keys(t).forEach(function(key: keyof BOSSTaksVariables){
		if (!t[key]) {
			delete t[key];
		}
	});
	return t;
}

function isEmptyString(value: any): string {
	if (typeof value == 'object' || !value) {
		return "";
	} else {
		return value;
	}
}
function isEmptyNumber(value: any): number {
	if (typeof value == 'object' || !value) {
		return 0;
	} else {
		return +value;
	}
}
function isEmptyBoolean(value: any): YesOrNo {
	if (typeof value == 'object' || !value) {
		return "";
	} else if (value == 'Y') {
		return 'Y';
	} else {
		return 'N';
	}
}

function isEmptyDate(value: any): Date {
	if (typeof value == 'object' || !value) {
		return null;
	} else {
		// Add regular expression check for format.
		var values: string[] = value.split(' ')[0].split('-');
		return new Date(+values[0], +values[1] - 1, +values[2]);
	}
}

class DataServiceImpl implements DataService {
	config: BOSSConfig = null;
	rights: BOSSUserRights = null;
	_user: BOSSUser = null;
	schema: TaskSchemaDefinition;

	setConfig(config: BOSSConfig): void {
		function trimAndRemoveTraillingSlash(url: string) {
			url = (url || '').trim();
			if (url[url.length - 1] == '/') {
				url = url.substr(0, url.length - 1);
			}
			return url;
		}
		
		config.contextPath = trimAndRemoveTraillingSlash(config.contextPath);
		config.url.boss = trimAndRemoveTraillingSlash(config.url.boss);
		config.url.mcd = trimAndRemoveTraillingSlash(config.url.mcd);
		config.url.logout = trimAndRemoveTraillingSlash(config.url.logout);

		this.config = config;
		const baseURL: string = config.url.boss // 'https://boss.us32d2.cz.nonprod/boss/api/';
		axiosInstance = axios.create({
			baseURL,
			timeout: 5000,
			headers: isLocalHost ? {} : {'X-Requested-With': 'XMLHttpRequest'},
			withCredentials: !isLocalHost
		});
		var w: any = window;
		// w['axiosInstance'] = axiosInstance;
		axiosInstance.interceptors.response.use(function(response: AxiosResponse) {
			// Do something with response data
			return Promise.resolve(response);
		}, ssoInterceptor);	
	}

	setUserRights(userRights: BOSSUserRights) {
		this.rights = userRights;
		Object.keys(userRights).forEach((key: keyof BOSSUserRights)=> {
			userRights[key] = userRights[key] || [];
			userRights[key] = userRights[key].sort((d1: string, d2: string)=> {
				return d1 > d2 ? 1 : -1;
			});
		});
	}

	api(path: string): string {
		return '' + path;
	}

	loadUser(): Promise<BOSSUser> {
		return new Promise<BOSSUser>((resolve, reject)=> {
			axios.all([axiosInstance.get('api/frm/user'), axiosInstance.get('api/boss/roles')])
			.then(axios.spread((userResponse: AxiosResponse, rolesResponse: AxiosResponse)=> {
				var user: BOSSUser = userResponse.data.content;
				user.roles = rolesResponse.data.content || [];
				user.rights = this.rights;
				this._user = user;
				user.hasRightTo = function(right: keyof BOSSUserRights, department: string): boolean {
					return (user.rights[right] || []).indexOf(department) > -1;
				};
				resolve(user);
			})).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});
	}

	logout() {
		var href: string = window.location.href;
		var uiURL: string = href.substring(0, href.lastIndexOf('#') > -1 ? href.lastIndexOf('#') : href.length );
		window.location.href = `${this.config.url.logout}?goto=${uiURL}`;
	}

	getUser() : BOSSUser {
		return this._user;
	}

	loadLanguages(): Promise<Array<BOSSLanguages>> {
		return new Promise<Array<BOSSLanguages>>((resolve, reject)=>{
			axiosInstance.get(`api/frm/languages`).then((response: AxiosResponse)=> {
				var data: Array<BOSSLanguages> = response.data.content;
				resolve(data);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});
	}

	loadLanguage(lang: string): Promise<Map<string, string>> {
		lang = lang || 'en';
		return new Promise<Map<string, string>>((resolve, reject)=>{
			axiosInstance.get(`api/frm/i18n/${lang}`).then((response: AxiosResponse)=> {
				var data: Map<string, string> = response.data.content;
				resolve(data);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});
	}


	loadSchemaDefinition(): Promise<TaskSchemaDefinition> {
		return new Promise<TaskSchemaDefinition>((resolve, reject)=>{
			axiosInstance.get(`/api/boss/process-definitions/${'LS'}/${'US_COMPLAINT'}/actions/start/schemaByKey`).then((response: AxiosResponse)=> {
				var schema: BOSSSchemaDefinition = response.data;
				var schemaDefinition: TaskSchemaDefinition = {};
				var requiredKeys: Array<keyof TaskSchemaDefinition> = [
					'FIRST_NAME', 
					'LAST_NAME', 
					'COMPLAINT_TYPE', 
					'COMPLAINT_RECEIVED_DATE', 
					'BCO_LEGAL_ESCALATED', 
					'LEVEL', 
					'SUMMARY_OF_FACTS'
				];
				Object.keys(schema.properties).forEach(function(key: keyof TaskSchemaDefinition) {
					var property: BOSSSchemaProperty = schema.properties[key];
					var schemaProperty : TaskSchemaProperty = {
						key,
						title: property.title,
						label: property.title,
						required: requiredKeys.indexOf(key) > -1
					};
					if (schemaProperty.required) {
						schemaProperty.label = `* ${schemaProperty.title}`;
					}
					schemaDefinition[key] = schemaProperty;
					if (!property.type && (property.enum && property.enum.length) && (property.enumTitles && property.enumTitles.length) && 
						property.enum.length == property.enumTitles.length) { // is enum
						var options: TaskSchemaPropertyOptions[] = [];
						property.enum.forEach(function(value: string, i: number) {
							options.push({
								value,
								key: property.enumTitles[i]
							});
						});
						schemaProperty.options = options;
					}
				});
				this.schema = schemaDefinition;
				resolve(schemaDefinition);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});
	}


	newTask() : Tasks {
		var user: BOSSUser = this._user;
		var variables: BOSSTaksVariables = {
			STATUS: 'pending_client',
			CREATED_BY_NAME: `${user.givenName} ${user.surname}`,
			CREATED_BY_USERNAME: user.commonName,
			TICKET_SOURCE: user.rights.CREATE_TICKET.length ? user.rights.CREATE_TICKET[0] : null
		};
		var task: Tasks = processTask({id: null, variables});
		task.files = [];
		return task;
	}

	public loadTasks(page: number = 0, type: string = 'inbox', filters: {name: string, value: string}[] = []): Promise<Results<Tasks>> {		
		return new Promise<Results<Tasks>>((resolve, reject)=>{
			var filter: string = ``;
			if (type) {
				filter = `staticFilterType.eq(${type})`;
			}
			filters = filters || [];
			
			filters.push({name: 'name', value: 'Resolve Complaint'});
			filters.push({name: 'processInstanceName', value: 'US LS - Complaint process'});
			
			filters.forEach((f: {name: string, value: string})=> {
				filter = `(${filter}).and(${f.name}.eq(${f.value}))`;
			});
			var params: any = {page};
			if (filter) {
				params.filter = filter;
			}
			axiosInstance.get('api/boss/tasks', {params: params}).then((response: AxiosResponse)=> {
				var data: BOSSTaks[] = response.data.content;
				var tasks: Tasks[] = data.map(function(t: BOSSTaks): Tasks{
					return processTask(t);
				});
				var meta: BOOSPageInfo = response.data.metaInfo.pageInfo;
				var results: Results<Tasks> = {
					records: tasks,
					count: meta.numberOfReturnedElements,
					total: meta.totalElements,
					pages: meta.totalPages,
					page: {
						index: meta.pageNumber,
						size: meta.pageSize,
						first: meta.isFirst,
						last: meta.isLast
					}
				};
				resolve(results);
			}).catch((error: AxiosError) => {
				var response: AxiosResponse = error.response;
				reject(new Error(response.data.message));
			});
		});
	}

	loadTask(pid: string, id: string): Promise<Tasks> {
		return new Promise<Tasks>((resolve, reject)=> {
			axios.all([axiosInstance.get(`/api/boss/processes/${pid}/tasks`), axiosInstance.get(`/api/boss/tasks/${id}/attachments`, {params: {size: 100}})])
			.then(axios.spread((response: AxiosResponse, attachementsResponse: AxiosResponse)=> {
				var tasks: BOSSTaks[] = response.data.content;
				var files: TaskAttachements[] = attachementsResponse.data.content || [];
				var task: Tasks;
				if (tasks && tasks.length) {
					task = processTask(tasks[0]);
					files.forEach(function(file: TaskAttachements){file.uploaded = true;});
					task.files = files;
					resolve(task);
				} else {
					reject();
				}
			})).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});
	}

	createTask(t: Tasks) : Promise<Tasks> {
		return new Promise<Tasks>((resolve, reject)=> {
			var bv: BOSSTaksVariables = unprocessTask(t);
			var errors: ValidationInfo = this.validateTask(t);
			if (errors.hasErrors == "true") {
				return reject(new Error('Not all required data are filled in!'));
			}
			var body: any = {
				content: bv,
				metaInfo: {},
				type: "stringMapRequest"
			};
			var task: Tasks;
			axiosInstance.post(`/api/boss/process-definitions/LS/US_COMPLAINT/actions/startByKey`, body).then((response: AxiosResponse) => {
				// Process Instance ID 
				var pid: any = +response.data.content;
				if (isNaN(pid)) {
					throw new Error('Invalid process ID returned.');
				}
				return axiosInstance.get(`/api/boss/processes/${pid}/tasks`);
			}).then((response: AxiosResponse)=> {
				var tasks: BOSSTaks[] = response.data.content;
				if (tasks && tasks.length) {
					task = processTask(tasks[0]);
					var uploadPromises: Promise<TaskAttachements>[] = [];
					uploadPromises = t.files.map((file: TaskAttachements)=> {
						return this.uploadFile(task, file);
					});
					return Promise.all(uploadPromises);
				} else {
					throw new Error('Could not create task!');
				}
			}).then((files: TaskAttachements[])=> {
				files.forEach(function(file: TaskAttachements){file.uploaded = true;});
				task.files = files;
				var user: BOSSUser = this._user;
				var departments: string[] = user.rights.CREATE_TICKET || [];
				if (departments.indexOf(task.ticket.departament) > -1) {
					return this.claimTask(task);
				} else {
					return Promise.resolve(task);
				}
			}).then((task: Tasks)=> {
				resolve(task);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});
	}

	updateTask(task: Tasks) : Promise<Tasks> {
		return new Promise<Tasks>((resolve, reject)=> {
			var bv: BOSSTaksVariables = unprocessTask(task);
			var errors: ValidationInfo = this.validateTask(task);
			if (errors.hasErrors == "true") {
				return reject(new Error('Not all required data are filled in!'));
			}
			var variables: any = bv;
			variables.type = "stringObjectAdaptedMap";
			axiosInstance.post(`/api/boss/tasks/${task.id}/${task.id}/form/actions/save`, {content: variables, metaInfo: {}, type: "stringMapRequest"}).then((response: AxiosResponse) => {
				var bossResponse: BOSSResponse = response.data;
				if (bossResponse.metaInfo.status !== "SUCCESS") {
					throw new Error('Unsuccessful task update.');
				}
				// Process Instance ID 
				if (!+bossResponse.content) {
					throw new Error('Invalid process ID returned.');
				}
				var uploadPromises: Promise<TaskAttachements>[] = [];
				uploadPromises = task.files.map((file: TaskAttachements)=> {
					if (file.uploaded) {
						return Promise.resolve(file);
					}
					return this.uploadFile(task, file);
				});
				return Promise.all(uploadPromises);
			}).then((files: TaskAttachements[]) => {
				task.files = files;
				files.forEach(function(file: TaskAttachements){file.uploaded = true;}); 
				resolve(task);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});
	}

	resolveTask(t: Tasks) : Promise<Tasks> {
		return new Promise<Tasks>((resolve, reject)=> {
			var bv: BOSSTaksVariables = unprocessTask(t);
			var errors: ValidationInfo = this.validateTask(t);
			if (errors.hasErrors == "true") {
				return reject(new Error('Not all required data are filled in!'));
			}
			var variables: any = bv;
			variables.type = "stringObjectAdaptedMap";
			axiosInstance.post(`/api/boss/tasks/${t.id}/form/actions/complete`, {content: variables, metaInfo: {}, type: "stringMapRequest"}).then((response: AxiosResponse) => {
				var bossResponse: BOSSResponse = response.data;
				if (bossResponse.metaInfo.status !== "SUCCESS") {
					throw new Error('Unsuccessful task resolving.');
				}
				resolve(t);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});
	}

	validateTask(task: Tasks) : ValidationInfo {
		var errors: ValidationInfo = {hasErrors: 'false'};
		var variables: BOSSTaksVariables = unprocessTask(task);
		var schema = this.schema;
		(task.files || []).some(function(file: TaskAttachements){
			if (!file.uploaded && (!file.name || !file.file) ) {
				errors.hasErrors = 'true';
				return true;
			}
			return false;
		});
		Object.keys(schema).forEach(function(key: keyof TaskSchemaDefinition){
			var property: TaskSchemaProperty = schema[key];
			if (property.required && !variables[key]) {
				errors[key] = property.title;
				errors.hasErrors = 'true';
			}
		});
		return errors;
	}
	
	claimTask(task: Tasks): Promise<Tasks> {
		return new Promise<Tasks>((resolve, reject)=> {
			axiosInstance.post(`/api/boss/tasks/${task.id}/actions/claim`, {}).then((response: AxiosResponse) => {
				var bossResponse: BOSSResponse = response.data;
				if (bossResponse.metaInfo.status !== "SUCCESS") {
					throw new Error('Unsuccessful user task assignment.');
				}
				return this.loadTask(task.process, task.id);
			}).then((task: Tasks)=> {
				resolve(task);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});	
	}

	assignTask(task: Tasks, user: string): Promise<boolean> {
		return new Promise<boolean>((resolve, reject)=> {
			var body: any = {
				content: {
					userId: user,
					type: "chooseUserActionDto"
				},
				type: "singularRequest"
			};
			axiosInstance.post(`/api/boss/tasks/${task.id}/actions/assign`, body).then((response: AxiosResponse) => {
				var bossResponse: BOSSResponse = response.data;
				if (bossResponse.metaInfo.status !== "SUCCESS") {
					throw new Error('Unsuccessful user task assignment.');
				}
				resolve(true);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});	
	}

	unassignTask(task: Tasks): Promise<boolean> {
		return new Promise<boolean>((resolve, reject)=> {
			axiosInstance.post(`/api/boss/tasks/${task.id}/actions/deassign`, {}).then((response: AxiosResponse) => {
				var bossResponse: BOSSResponse = response.data;
				if (bossResponse.metaInfo.status !== "SUCCESS") {
					throw new Error('Unsuccessful task unassignment.');
				}
				resolve(true);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});	
	}

	getAllowedUsers(task: Tasks): Promise<BOSSUsers[]> {
		return new Promise<BOSSUsers[]>((resolve, reject)=> {
			axiosInstance.get(`/api/boss/tasks/${task.id}/allowed-users`, {}).then((response: AxiosResponse) => {
				var bossResponse: BOSSResponse = response.data;
				if (bossResponse.metaInfo.status !== "SUCCESS") {
					throw new Error('Unsuccessful user list retrieving.');
				}
				var users: BOSSUsers[] = bossResponse.content || [];
				resolve(users);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});
	}

	uploadFile(task: Tasks, file: TaskAttachements): Promise<TaskAttachements> {
		return new Promise<TaskAttachements>((resolve, reject)=> {
			var data = new FormData();
			data.append('type', 'attachmentUploadActionDto');
			data.append('_requestDataType', 'singularRequest');
			data.append('title', file.name);
			data.append('file', file.file);
			axiosInstance.post(`/api/boss/tasks/${task.id}/actions/attach`, data).then((response: AxiosResponse) => {
				var bossResponse: BOSSResponse = response.data;
				if (bossResponse.metaInfo.status !== "SUCCESS") {
					throw new Error('Unsuccessful file upload.');
				}
				resolve(file);
			}).catch((error: AxiosError)=> {
				reject(this._processError(error));
			});
		});
	}	

	_processError(error: AxiosError) : {message: string | string[]} | Error {
		/*
			Possibly something like this:
			{
			  "type": "singularResponse",
			  "metaInfo": {
			    "status": "ERROR",
			    "messages": [
			      "Activiti exception was thrown: \"Task '36' is already claimed by someone else.\""
			    ]
			  },
			  "content": null
			}
		*/

		console.error(error);

		var response: AxiosResponse = error.response;
		var message = ['message.internalServerError', 'message.internalServerErrorDescription'];
		if (response) {
			if (response.data) { // A response was received from the server
				var data: BOSSResponse = response.data;
				if (data.type == "singularResponse" && data.metaInfo && data.metaInfo.status == "ERROR") {
					message = data.metaInfo.messages;
				}
			}
		}
		return {message};
	}
}

interface BOSSResponse {
	type: "collectionResponse" | "singularResponse",
	metaInfo: {
		status: string,
		messages: string[]
	},
	content: any
}

export const dataService: DataService = new DataServiceImpl();



