import * as React from 'react';

import Dialog from 'react-toolbox/lib/dialog';
import { Snackbar } from 'react-toolbox/lib/snackbar';
import Input from 'react-toolbox/lib/input';
import Checkbox from 'react-toolbox/lib/checkbox';
import { BrowserRouter as Router, Route, Link, RouteComponentProps } from 'react-router-dom';
import * as H from 'history';
import { format } from 'date-fns';

var ReactPaginate = require('react-paginate'); 

import {
    FormattedMessage,
    FormattedNumber,
    FormattedRelative,
    InjectedIntlProps,
    injectIntl
} from 'react-intl';

import { Table, TableHead, TableRow, TableCell } from 'react-toolbox/lib/table';
import {Button} from 'react-toolbox/lib/button';
import { dataService, ValidationInfo } from '../data-service';
import { logger } from '../logger';

import { Tasks, Results, BOSSUser, BOSSUsers, BOSSUserRightKeys, TaskSchemaDefinition, TaskSchemaProperty, TaskSchemaPropertyOptions, TaskAttachements, YesOrNo} from '../models';

import * as PubSub from 'pubsub-js';

import { withRouter } from 'react-router-dom';
import FontIcon from 'react-toolbox/lib/font_icon';
import { Dropdown } from 'react-toolbox/lib/dropdown';
import DatePicker from 'react-toolbox/lib/date_picker';

interface NewTicketProps extends RouteComponentProps<{id?: string, pid?: string}>, InjectedIntlProps {

}

interface NewTicketState {
    user?: BOSSUser;
    task?: Tasks;
    hasErrors?: boolean;
    modified?: boolean;
    assigned?: boolean;

    modals: {
        assignTask?: {
            users?: BOSSUsers[],
            user?: string,
            actions?: any
        };
        changeDepartment?: {
            departments?: TaskSchemaPropertyOptions[],
            department?: string,
            actions?: any
        };
    }
}

class NewTicketWrapped extends React.Component<NewTicketProps, NewTicketState> {

	state: NewTicketState = {
        
        modals: {
            
        }
	};

    private removeLocationListner: any;

	constructor(props: NewTicketProps) {
		super(props);
	}

	componentDidMount() {
        logger.log('Complaint Ticket');
        var user: BOSSUser = dataService.getUser();
        this.setState({user})
        this.removeLocationListner = this.props.history.listen((location: H.Location, action: "PUSH" | "POP" | "REPLACE") => {
            setTimeout(()=> {
                this.locationUpdated();
            }, 0)
        });
        this.locationUpdated();
	}

    locationUpdated() {
        var pid: string = this.props.match.params.pid;
        var id: string = this.props.match.params.id;
        if (id && pid) {
            logger.group(`Loading existing ticket: Process<${pid}> Task<${id}>`);
            dataService.loadTask(pid, id).then((task: Tasks)=> {
                logger.log('Loaded ticket: ', task);
                this.setState({task, modified: false, assigned: task.original.assignee == this.state.user.id});
                logger.ungroup();
            }).catch((error)=> {
                logger.error(error);
                PubSub.publish('ERROR', `Could not load complaint ticket: ${id}`);
                this.props.history.push(`/`);
                logger.ungroup();
            });
        } else {
            logger.log('Creating new ticket');
            this.setState({
                assigned: true,
                task: dataService.newTask()
            });
        }
    }

	componentWillUnmount() {
        this.removeLocationListner();
	}

    createComplaint() {
        logger.group('Saving new ticket');
        dataService.createTask(this.state.task).then((task: Tasks) => {
            logger.log('Saved ticket: ', task);
            var assigned: boolean = task.original.assignee == dataService.getUser().id;
            this.setState({task, modified: false, assigned});
            if (assigned) {
                logger.log('Ticket assigned to user. Redirecting...');
                this.props.history.push(`/complaint/${task.process}/${task.id}`);
                PubSub.publish('INFO', 'Complaint ticket has been created and assigned to you.');
            } else {
                logger.log('Ticket not assigned to user. Redirecting...');
                this.props.history.push(`/`);
                PubSub.publish('INFO', 'Complaint ticket has been created.');
            }
            logger.ungroup();
        }).catch((error)=> {
            logger.error(error);
            PubSub.publish('ERROR', `Could not create complaint ticket.`);
            this.props.history.push(`/`);
            logger.ungroup();
        });
    }

    updateComplaint() {
        logger.group('Updating existing ticket');
        dataService.updateTask(this.state.task).then((task: Tasks) => {
            logger.log('Updated existing ticket: ', task);
            this.setState({task, modified: false});
            PubSub.publish('INFO', 'Complaint ticket has been updated.');
            logger.ungroup();
        }).catch((error)=> {
            logger.error(error);
            PubSub.publish('ERROR', `Could not update complaint ticket.`);
            this.props.history.push(`/`);
            logger.ungroup();
        });
    }

    resolveComplaint() {
        logger.group('Resolving existing ticket');
        dataService.resolveTask(this.state.task).then((task: Tasks) => {
            logger.log('Resolved existing ticket: ', task);
            this.props.history.push(`/`);
            PubSub.publish('INFO', 'Complaint ticket has been marked as resolved.');
            logger.ungroup();
        }).catch((error)=> {
            logger.error(error);
            PubSub.publish('ERROR', `Could not resolve complaint ticket.`);
            this.props.history.push(`/`);
            logger.ungroup();
        });
    }

    claimTicket() {
        var {user, task} = this.state;
        logger.group(`Claiming ticket for user <${user.id}>`);
        dataService.claimTask(task).then((task: Tasks)=> {
            logger.log(`Ticket successfully claimed for this user.`);
            this.setState({task, assigned: task.original.assignee == user.id});
            PubSub.publish('INFO', 'Complaint ticket has been assigned to you.');
            logger.ungroup();
        }).catch((error)=> {
            logger.error(error);
            PubSub.publish('ERROR', `Could not claim complaint ticket. It might have been assigned to a different user already.`);
            logger.ungroup();
        });  
    }

    unassignTicket() {
        var {user, task} = this.state;
        logger.group('Unassigning ticket from current user.');
        dataService.unassignTask(task).then((unassigned: boolean)=> {
            logger.log('Unassigned result: ', unassigned);
            this.setState({assigned: !unassigned});
            return dataService.loadTask(task.process, task.id);
        }).then((task: Tasks)=> {
            logger.log('Task reloaded: ', task);
            this.setState({task});
            PubSub.publish('INFO', 'Complaint ticket has been unassigned.');
            logger.ungroup();
        }).catch((error)=> {
            logger.error(error);
            PubSub.publish('ERROR', `Could not unassign complaint ticket.`);
            logger.ungroup();
        }); 
    }

    assignTicket() {
        var {user, task} = this.state;
        logger.group('Ticket re-assgiment.');
        logger.log('Loading allowed users');
        dataService.getAllowedUsers(task).then((users: BOSSUsers[])=> {
            logger.log('List of users loaded: ', users);
            this.setState({modals: {assignTask: {
                users,
                actions: [
                    { label: "Cancel", onClick: ()=> {
                        logger.log('Ticket reassigment canceled.')
                        this.setState({modals: {assignTask: null}});
                        logger.ungroup();
                    }, className: "xr-action__cancel" },
                    { label: "Assign", onClick: () => {
                        var {task} = this.state;
                        if (!this.state.modals.assignTask.user) {
                            return;
                        }
                        logger.log('Assigning ticket to user: ', this.state.modals.assignTask.user);
                        dataService.assignTask(task, this.state.modals.assignTask.user).then((reassigned: boolean)=> {
                            logger.log('Ticked re-assigned: ', reassigned, 'reloading ticket.');
                            this.setState({task, assigned: !reassigned});
                            return dataService.loadTask(task.process, task.id);
                        }).then((task: Tasks)=> {
                            logger.log('Ticket reloaded', task);
                            this.setState({task, modals: {assignTask: null}});
                            PubSub.publish('INFO', 'Complaint ticket has been reassigned.');
                            logger.ungroup();
                        }).catch((error)=> {
                            logger.error(error);
                            PubSub.publish('ERROR', 'Could not assign complaint ticket to selected user.');
                            logger.ungroup();
                        });
                    }, className: "xr-action__accept" }
                ]
            }}});
        }).catch((error)=> {
            logger.error(error);
            PubSub.publish('ERROR', `Could not load the list of users.`);
            logger.ungroup();
        });
    }

    changeDepartment() {
        var {user, task} = this.state;
        var {schema} = dataService;
        var levelDepartments: TaskSchemaPropertyOptions[] = schema.RESOLVING_DEPARTMENT.options.filter(function(option: TaskSchemaPropertyOptions){
            return option.key.indexOf(`.level.1.`) > -1;
        });

        levelDepartments.filter(function(department: TaskSchemaPropertyOptions) {
            return user.rights.CHANGE_DEPARTMENT.indexOf(department.value) > -1;
        });
        logger.group('Changing ticket department');
        Promise.resolve(levelDepartments).then((departments: TaskSchemaPropertyOptions[])=> {
            this.setState({modals: {changeDepartment: {
                departments,
                department: task.ticket.departament,
                actions: [
                    { label: "Cancel", onClick: ()=> {
                        logger.log('Department change canceled.')
                        this.setState({modals: {changeDepartment: null}});
                        logger.ungroup();
                    }, className: "xr-action__cancel" },
                    { label: "Change", onClick: () => {
                        var {task} = this.state;
                        if (!this.state.modals.changeDepartment.department) {
                            return;
                        }
                        logger.log('Changing department to: ', this.state.modals.changeDepartment.department);
                        task.ticket.departament = this.state.modals.changeDepartment.department;
                        dataService.updateTask(task).then((task: Tasks)=> {
                            logger.log('Ticket updated: ', task);
                            this.setState({task});
                            return dataService.loadTask(task.process, task.id);
                        }).then((task: Tasks)=> {
                            logger.log('Ticket reloaded: ', task);
                            this.setState({task, modals: {changeDepartment: null}});
                            PubSub.publish('INFO', 'Department has been changed');
                            logger.ungroup();
                        }).catch((error)=> {
                            logger.error(error);
                            PubSub.publish('ERROR', `Could not change complaint ticket to selected department.`);
                            logger.ungroup();
                        });
                    }, className: "xr-action__accept" }
                ]
            }}});
        }).catch((error)=> {
            logger.error(error);
            PubSub.publish('ERROR', error);
            logger.ungroup();
        });;
    }

    update() {
        var validation: ValidationInfo = dataService.validateTask(this.state.task);
        this.setState({modified: true, hasErrors: validation.hasErrors == "true"});
    }

    updateLevel() {
        var {task} = this.state;
        if (task.complaint.escalated == 'Y' || task.complaint.type == 'legal_or_regulatory') {
            task.complaint.level = '3';
        }
        if (task.complaint.level == '3') {
            task.ticket.departament = 'legal';
        } else if (task.complaint.level == '2') {
            task.ticket.departament = 'operations';
        } else if (task.complaint.level == '1') {
            var {schema} = dataService;
            var levelDepartments: TaskSchemaPropertyOptions[] = schema.RESOLVING_DEPARTMENT.options.filter(function(option: TaskSchemaPropertyOptions){
                return option.key.indexOf(`.level.1.`) > -1;
            });
            var user: BOSSUser = dataService.getUser();
            var departments: string[] = user.rights.CREATE_TICKET || [];
            levelDepartments = levelDepartments.filter(function(option: TaskSchemaPropertyOptions){
                return departments.indexOf(option.value) > -1;
            });
            levelDepartments = levelDepartments.sort(function(option1: TaskSchemaPropertyOptions, option2: TaskSchemaPropertyOptions) {
                return option1.value > option2.value ? 1 : -1;
            });
            if (levelDepartments.length) {
                task.ticket.departament = levelDepartments[0].value;
            }
        } else {
            task.ticket.departament = '';
        }
    }

    formatDate(value: string) {
        if (!value) {
            return null;
        } else {
            return new Date(value);
        }
    }

    getSubCategoryOptions(value: string, category: TaskSchemaProperty, subcategory: TaskSchemaProperty): TaskSchemaPropertyOptions[] {
        var options: TaskSchemaPropertyOptions[] = [];
        if (!value) {
            return options;
        }
        var option: TaskSchemaPropertyOptions = category.options.find((option: TaskSchemaPropertyOptions)=> {
            return option.value == value;
        });

        options = subcategory.options.filter((suboption: TaskSchemaPropertyOptions)=> {
            return suboption.key.indexOf(`${option.key}.`) == 0;
        });

        return options;
    } 

	render() {
        var schema: TaskSchemaDefinition = dataService.schema;
        var {task} = this.state;
        if (!task) {
            return <div></div>;
        }
		return (
			<div className="col-xs-12 new-ticket-container">
                {
                    task.id?
                    <div className="customer-info-content">
                        <div className="row">
                            <div className="col-xs-8">
                                {
                                    this.state.assigned ?
                                    <Button label="Unassign ticket from me" raised primary 
                                        onClick={()=> {this.unassignTicket()}}
                                        disabled={!this.state.user.hasRightTo(BOSSUserRightKeys.UNASSIGN_TICKET, task.ticket.departament)}/>
                                    : 
                                    <Button label="Assign ticket to me" raised primary 
                                        onClick={()=> {this.claimTicket()}}
                                        disabled={this.state.user.hasRightTo(BOSSUserRightKeys.ASSIGN_TICKET, task.ticket.departament)}/>
                                }
                                {
                                    this.state.assigned && this.state.user.hasRightTo(BOSSUserRightKeys.ASSIGN_TICKET, task.ticket.departament)?
                                    <Button label="Assign ticket" raised primary style={{marginLeft: '20px'}}
                                        onClick={()=> {this.assignTicket()}}/>
                                    : 
                                    null
                                }
                                {
                                    this.state.assigned && this.state.user.hasRightTo(BOSSUserRightKeys.CHANGE_DEPARTMENT, task.ticket.departament)?
                                    <Button label="Change department" raised primary style={{marginLeft: '20px'}}
                                        onClick={()=> {this.changeDepartment()}} />
                                    : null
                                }
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.TICKET_ID.required}>
                                {schema.TICKET_ID.label}
                            </div>
                            <div className="col-xs-2">
                                <Input type='text' disabled={true} 
                                    name={schema.TICKET_ID.key} 
                                    value={task.ticket.id} 
                                    required={schema.TICKET_ID.required} />
                            </div>
                        </div>
                    </div>
                    : null
                }
                {/* Customer Info */}
                <div className="customer-info-container">
                    <span className="customer-info-name">Customer Info</span>
                    <div className="customer-info-content">
                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.FIRST_NAME.required}>
                                {schema.FIRST_NAME.label}
                            </div>
                            <div className="col-xs-4">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.FIRST_NAME.key} 
                                    value={task.customer.name} 
                                    onChange={(value: string)=> {task.customer.name = value; this.update()}}
                                    required={schema.FIRST_NAME.required} />
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.LAST_NAME.required}>
                                {schema.LAST_NAME.label}
                            </div>
                            <div className="col-xs-4">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.LAST_NAME.key} 
                                    value={task.customer.surname} 
                                    onChange={(value: string)=> {task.customer.surname = value; this.update()}}
                                    required={schema.LAST_NAME.required} />
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.ADDRESS.required}>
                                {schema.ADDRESS.label}
                            </div>
                            <div className="col-xs-4">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.ADDRESS.key} 
                                    value={task.customer.address} 
                                    onChange={(value: string)=> {task.customer.address = value; this.update()}}
                                    required={schema.ADDRESS.required} />
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.EMAIL.required}>
                                {schema.EMAIL.label}
                            </div>
                            <div className="col-xs-4">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.EMAIL.key} 
                                    value={task.customer.email} 
                                    onChange={(value: string)=> {task.customer.email = value; this.update()}}
                                    required={schema.EMAIL.required} />
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.STATE.required}>
                                {schema.STATE.label}
                            </div>
                            <div className="col-xs-2">
                                <Dropdown disabled={!this.state.assigned}
                                    source={schema.STATE.options}
                                    value={task.customer.state}
                                    onChange={(value: string)=> {task.customer.state = value; this.update();}}/>
                            </div>
                            <div className="col-xs-1 form-label" data-required={schema.ZIP.required}>
                                {schema.ZIP.label}
                            </div>
                            <div className="col-xs-1">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.ZIP.key} 
                                    value={task.customer.zip} 
                                    onChange={(value: string)=> {task.customer.zip = value; this.update()}}
                                    required={schema.ZIP.required} />
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.PHONE.required}>
                                {schema.PHONE.label}
                            </div>
                            <div className="col-xs-4">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.PHONE.key} 
                                    value={task.customer.phone} 
                                    onChange={(value: string)=> {task.customer.phone = value; this.update()}}
                                    required={schema.PHONE.required} />
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.CMS_ACCOUNT_ID.required}>
                                {schema.CMS_ACCOUNT_ID.label}
                            </div>
                            <div className="col-xs-4">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.CMS_ACCOUNT_ID.key} 
                                    value={task.customer.accountId} 
                                    onChange={(value: string)=> {task.customer.accountId = value; this.update()}}
                                    required={schema.CMS_ACCOUNT_ID.required} />
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.PAN.required}>
                                {schema.PAN.label}
                            </div>
                            <div className="col-xs-4">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.PAN.key} 
                                    value={task.customer.pan} 
                                    onChange={(value: string)=> {task.customer.pan = value; this.update()}}
                                    required={schema.PAN.required} />
                            </div>
                        </div>

                        {   
                            task.id ?
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.FOUND_ACCOUNT_ID.required}>
                                    {schema.FOUND_ACCOUNT_ID.label}
                                </div>
                                <div className="col-xs-4">
                                    <Input type='text' disabled={true}
                                        name={schema.FOUND_ACCOUNT_ID.key} 
                                        value={task.customer.foundAccountId} 
                                        required={schema.FOUND_ACCOUNT_ID.required} />
                                </div>
                            </div>
                            : null
                        }
                    </div>
                </div>

                {/* Complaint Info */}
                <div className="customer-info-container">
                    <span className="customer-info-name">Complaint Info</span>
                    <div className="customer-info-content">
                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.COMPLAINT_TYPE.required}>
                                {schema.COMPLAINT_TYPE.label}
                            </div>
                            <div className="col-xs-4">
                                <Dropdown disabled={!this.state.assigned}
                                    source={schema.COMPLAINT_TYPE.options}
                                    value={task.complaint.type}
                                    onChange={(value: string)=> {task.complaint.type = value; this.updateLevel(); this.update();}}/>
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.COMPLAINT_RECEIVED_DATE.required}>
                                {schema.COMPLAINT_RECEIVED_DATE.label}
                            </div>
                            <div className="col-xs-4 form-datepicker">
                                <DatePicker readonly={!this.state.assigned}
                                    name={schema.COMPLAINT_RECEIVED_DATE.key} inputFormat={(value: Date)=> format(value, 'MM/DD/YYYY')}
                                    onChange={(value: Date)=> { task.complaint.dateReceived = value; this.update();} }
                                    value={task.complaint.dateReceived} 
                                    />
                                <FontIcon value="event" />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.BCO_LEGAL_ESCALATED.required}>
                                {schema.BCO_LEGAL_ESCALATED.label}
                            </div>
                            <div className="col-xs-4">
                                <Dropdown disabled={!this.state.assigned}
                                    source={schema.BCO_LEGAL_ESCALATED.options}
                                    value={task.complaint.escalated}
                                    onChange={(value: YesOrNo)=> {task.complaint.escalated = value; this.updateLevel(); this.update();}}/>
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.LEVEL.required}>
                                {schema.LEVEL.label}
                            </div>
                            <div className="col-xs-4">
                                <Dropdown disabled={!this.state.assigned || task.complaint.escalated == 'Y' || task.complaint.type == 'legal_or_regulatory'}
                                    source={schema.LEVEL.options}
                                    value={task.complaint.level}
                                    onChange={(value: string)=> {task.complaint.level = value; this.updateLevel(); this.update();}}/>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.COMPLAINT_SOURCE.required}>
                                {schema.COMPLAINT_SOURCE.label}
                            </div>
                            <div className="col-xs-4">
                                <Dropdown disabled={!this.state.assigned}
                                    source={schema.COMPLAINT_SOURCE.options}
                                    value={task.complaint.source}
                                    onChange={(value: string)=> {task.complaint.source = value; this.update();}}/>
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.METHOD_RECEIVED.required}>
                                {schema.METHOD_RECEIVED.label}
                            </div>
                            <div className="col-xs-4">
                                <Dropdown disabled={!this.state.assigned}
                                    source={schema.METHOD_RECEIVED.options}
                                    value={task.complaint.methodReceived}
                                    onChange={(value: string)=> {task.complaint.methodReceived = value; this.update();}}/>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.COMPLAINT_CATEGORY.required}>
                                {schema.COMPLAINT_CATEGORY.label}
                            </div>
                            <div className="col-xs-4">
                                <Dropdown disabled={!this.state.assigned}
                                    source={schema.COMPLAINT_CATEGORY.options}
                                    value={task.complaint.category.main}
                                    onChange={(value: string)=> {task.complaint.category.main = value; this.update();}}/>
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.CIMS_NUMBER.required}>
                                {schema.CIMS_NUMBER.label}
                            </div>
                            <div className="col-xs-4">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.CIMS_NUMBER.key} 
                                    value={task.complaint.cimsNumber} 
                                    onChange={(value: string)=> {task.complaint.cimsNumber = value; this.update()}}
                                    required={schema.CIMS_NUMBER.required} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.COMPLAINT_SUBCATEGORY.required}>
                                {schema.COMPLAINT_SUBCATEGORY.label}
                            </div>
                            <div className="col-xs-4">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.COMPLAINT_SUBCATEGORY.key} 
                                    value={task.complaint.category.second} 
                                    onChange={(value: string)=> {task.complaint.category.second = value; this.update()}}
                                    required={schema.COMPLAINT_SUBCATEGORY.required} />
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.HCUS_COMPLAINT_CATEGORY.required}>
                                {schema.HCUS_COMPLAINT_CATEGORY.label}
                            </div>
                            <div className="col-xs-4">
                                <Dropdown disabled={!this.state.assigned}
                                    source={schema.HCUS_COMPLAINT_CATEGORY.options}
                                    value={task.complaint.category.hcus.main}
                                    onChange={(value: string)=> {task.complaint.category.hcus.main = value; task.complaint.category.hcus.second = ''; this.update();}}/>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.COMPLAINT_OTHER_CATEGORY.required}>
                                {schema.COMPLAINT_OTHER_CATEGORY.label}
                            </div>
                            <div className="col-xs-4">
                                <Input type='text' disabled={!this.state.assigned}
                                    name={schema.COMPLAINT_OTHER_CATEGORY.key} 
                                    value={task.complaint.category.other} 
                                    onChange={(value: string)=> {task.complaint.category.other = value; this.update()}}
                                    required={schema.COMPLAINT_OTHER_CATEGORY.required} />
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.HCUS_COMPLAINT_SUBCATEGORY.required}>
                                {schema.HCUS_COMPLAINT_SUBCATEGORY.label}
                            </div>
                            <div className="col-xs-4">
                                <Dropdown disabled={!this.state.assigned || !task.complaint.category.hcus.main}
                                    source={this.getSubCategoryOptions(task.complaint.category.hcus.main, schema.HCUS_COMPLAINT_CATEGORY, schema.HCUS_COMPLAINT_SUBCATEGORY)}
                                    value={task.complaint.category.hcus.second}
                                    onChange={(value: string)=> {task.complaint.category.hcus.second = value; this.update();}}/>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.CAUSED_BY_FD.required}>
                                {schema.CAUSED_BY_FD.label}
                            </div>
                            <div className="col-xs-4">
                                <Dropdown disabled={!this.state.assigned}
                                    source={schema.CAUSED_BY_FD.options}
                                    value={task.complaint.causedByFD}
                                    onChange={(value: YesOrNo)=> {task.complaint.causedByFD = value; this.update();}}/>
                            </div>
                            <div className="col-xs-2 form-label" data-required={schema.DEPARTMENT_ORIGINATED.required}>
                                {schema.DEPARTMENT_ORIGINATED.label}
                            </div>
                            <div className="col-xs-4">
                                <Dropdown disabled={!this.state.assigned}
                                    source={schema.DEPARTMENT_ORIGINATED.options || []}
                                    value={task.complaint.origin}
                                    onChange={(value: string)=> {task.complaint.origin = value; this.update();}}/>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.SUMMARY_OF_FACTS.required}>
                                {schema.SUMMARY_OF_FACTS.label}
                            </div>
                            <div className="col-xs-10">
                                <textarea rows={10} disabled={!this.state.assigned}
                                    name={schema.SUMMARY_OF_FACTS.key} 
                                    value={task.complaint.summary} 
                                    onChange={(event: any)=> {task.complaint.summary = event.target.value; this.update();}}
                                    required={schema.SUMMARY_OF_FACTS.required} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-2 form-label" data-required={schema.DESIRED_RESOLUTION.required}>
                                {schema.DESIRED_RESOLUTION.label}
                            </div>
                            <div className="col-xs-10">
                                <textarea rows={10} disabled={!this.state.assigned}
                                    name={schema.DESIRED_RESOLUTION.key} 
                                    value={task.complaint.desiredResolution} 
                                    onChange={(event: any)=> {task.complaint.desiredResolution = event.target.value; this.update();}}
                                    required={schema.DESIRED_RESOLUTION.required} />
                            </div>
                        </div>
                    </div>
                </div>

                {/* Resolution Info */}
                {
                    task.id ?
                    <div className="customer-info-container">
                        <span className="customer-info-name">Resolution Info</span>
                        <div className="customer-info-content">
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.FINDINGS.required}>
                                    {schema.FINDINGS.label}
                                </div>
                                <div className="col-xs-10">
                                    <textarea rows={10} disabled={!this.state.assigned}
                                        name={schema.FINDINGS.key} 
                                        value={task.resolution.findings}
                                        onChange={(event: any)=> {task.resolution.findings = event.target.value; this.update();}} 
                                        required={schema.FINDINGS.required} />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.MONETARY_RELIEF_AMOUNT.required}>
                                    {schema.MONETARY_RELIEF_AMOUNT.label}
                                </div>
                                <div className="col-xs-4">
                                    <Input type='text' disabled={!this.state.assigned}
                                        name={schema.MONETARY_RELIEF_AMOUNT.key} 
                                        value={task.resolution.monetaryRelief.amount} 
                                        onChange={(value: string)=> {task.resolution.monetaryRelief.amount = value; this.update()}}
                                        required={schema.MONETARY_RELIEF_AMOUNT.required} />
                                </div>
                                <div className="col-xs-2 form-label" data-required={schema.CALL_ATTEMPTS_COUNT.required}>
                                    {schema.CALL_ATTEMPTS_COUNT.label}
                                </div>
                                <div className="col-xs-4">
                                    <Input type='text' disabled={!this.state.assigned}
                                        name={schema.CALL_ATTEMPTS_COUNT.key} 
                                        value={task.resolution.calls.attempts} 
                                        onChange={(value: string)=> {if(!isNaN(+value)) {task.resolution.calls.attempts = value}; this.update()}}
                                        required={schema.CALL_ATTEMPTS_COUNT.required} />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.MONETARY_RELIEF_OTHER.required}>
                                    {schema.MONETARY_RELIEF_OTHER.label}
                                </div>
                                <div className="col-xs-4">
                                    <Input type='text' disabled={!this.state.assigned}
                                        name={schema.MONETARY_RELIEF_OTHER.key} 
                                        value={task.resolution.monetaryRelief.other} 
                                        onChange={(value: string)=> {task.resolution.monetaryRelief.other = value; this.update()}}
                                        required={schema.MONETARY_RELIEF_OTHER.required} />
                                </div>
                                <div className="col-xs-2 form-label" data-required={schema.CALL_1_DATE.required}>
                                    {schema.CALL_1_DATE.label}
                                </div>
                                <div className="col-xs-4 form-datepicker">
                                    <DatePicker readonly={!this.state.assigned}
                                        name={schema.CALL_1_DATE.key} inputFormat={(value: Date)=> format(value, 'MM/DD/YYYY')}
                                        onChange={(value: Date)=> { task.resolution.calls.first = value; this.update();} }
                                        value={task.resolution.calls.first} 
                                        />
                                    <FontIcon value="event" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.RESPONSE_METHOD.required}>
                                    {schema.RESPONSE_METHOD.label}
                                </div>
                                <div className="col-xs-4">
                                    <Dropdown disabled={!this.state.assigned}
                                        source={schema.RESPONSE_METHOD.options || []}
                                        value={task.resolution.response.method}
                                        onChange={(value: string)=> {task.resolution.response.method = value; this.update();}}/>
                                </div>
                                <div className="col-xs-2 form-label" data-required={schema.CALL_2_DATE.required}>
                                    {schema.CALL_2_DATE.label}
                                </div>
                                <div className="col-xs-4 form-datepicker">
                                    <DatePicker readonly={!this.state.assigned}
                                        name={schema.CALL_2_DATE.key} inputFormat={(value: Date)=> format(value, 'MM/DD/YYYY')}
                                        onChange={(value: Date)=> { task.resolution.calls.second = value; this.update();} }
                                        value={task.resolution.calls.second} 
                                        />
                                    <FontIcon value="event" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.RESPONSE_REJECTED.required}>
                                    {schema.RESPONSE_REJECTED.label}
                                </div>
                                <div className="col-xs-4">
                                    <Dropdown disabled={!this.state.assigned}
                                        source={schema.RESPONSE_REJECTED.options || []}
                                        value={task.resolution.response.rejected}
                                        onChange={(value: YesOrNo)=> {task.resolution.response.rejected = value; this.update();}}/>
                                </div>
                                <div className="col-xs-2 form-label" data-required={schema.CALL_3_DATE.required}>
                                    {schema.CALL_3_DATE.label}
                                </div>
                                <div className="col-xs-4 form-datepicker">
                                    <DatePicker readonly={!this.state.assigned}
                                        name={schema.CALL_3_DATE.key} inputFormat={(value: Date)=> format(value, 'MM/DD/YYYY')}
                                        onChange={(value: Date)=> { task.resolution.calls.third = value; this.update();} }
                                        value={task.resolution.calls.third} 
                                        />
                                    <FontIcon value="event" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.CONCLUSION.required}>
                                    {schema.CONCLUSION.label}
                                </div>
                                <div className="col-xs-4">
                                    <Dropdown disabled={!this.state.assigned}
                                        source={schema.CONCLUSION.options || []}
                                        value={task.resolution.conclusion}
                                        onChange={(value: string)=> {task.resolution.conclusion = value; this.update();}}/>
                                </div>
                                <div className="col-xs-2 form-label" data-required={schema.CALL_4_DATE.required}>
                                    {schema.CALL_4_DATE.label}
                                </div>
                                <div className="col-xs-4 form-datepicker">
                                    <DatePicker readonly={!this.state.assigned}
                                        name={schema.CALL_4_DATE.key} inputFormat={(value: Date)=> format(value, 'MM/DD/YYYY')}
                                        onChange={(value: Date)=> { task.resolution.calls.fourth = value; this.update();} }
                                        value={task.resolution.calls.fourth} 
                                        />
                                    <FontIcon value="event" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.STATUS.required}>
                                    {schema.STATUS.label}
                                </div>
                                <div className="col-xs-4">
                                    <Dropdown disabled={!this.state.assigned}
                                        source={schema.STATUS.options || []}
                                        value={task.resolution.status}
                                        onChange={(value: string)=> {task.resolution.status = value; this.update();}}/>
                                </div>
                                <div className="col-xs-2 form-label" data-required={schema.SUBMISSION_DATE.required}>
                                    {schema.SUBMISSION_DATE.label}
                                </div>
                                <div className="col-xs-4 form-datepicker">
                                    <DatePicker readonly={!this.state.assigned}
                                        name={schema.SUBMISSION_DATE.key} inputFormat={(value: Date)=> format(value, 'MM/DD/YYYY')}
                                        onChange={(value: Date)=> { task.resolution.submissionDate = value; this.update();} }
                                        value={task.resolution.submissionDate} 
                                        />
                                    <FontIcon value="event" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.LETTER_SENT.required}>
                                    {schema.LETTER_SENT.label}
                                </div>
                                <div className="col-xs-4">
                                    <Dropdown disabled={!this.state.assigned}
                                        source={schema.LETTER_SENT.options || []}
                                        value={task.resolution.letter.sent}
                                        onChange={(value: YesOrNo)=> {task.resolution.letter.sent = value; this.update();}}/>
                                </div>
                                <div className="col-xs-2 form-label" data-required={schema.CIMS_CLOSED_DATE.required}>
                                    {schema.CIMS_CLOSED_DATE.label}
                                </div>
                                <div className="col-xs-4 form-datepicker">
                                    <DatePicker readonly={!this.state.assigned}
                                        name={schema.CIMS_CLOSED_DATE.key} inputFormat={(value: Date)=> format(value, 'MM/DD/YYYY')}
                                        onChange={(value: Date)=> { task.resolution.cimsClosedDate = value; this.update();} }
                                        value={task.resolution.cimsClosedDate} 
                                        />
                                    <FontIcon value="event" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.LETTER_SENT_DATE.required}>
                                    {schema.LETTER_SENT_DATE.label}
                                </div>
                                <div className="col-xs-4 form-datepicker">
                                    <DatePicker readonly={!this.state.assigned}
                                        name={schema.LETTER_SENT_DATE.key} inputFormat={(value: Date)=> format(value, 'MM/DD/YYYY')}
                                        onChange={(value: Date)=> { task.resolution.letter.date = value; this.update();} }
                                        value={task.resolution.letter.date} 
                                        />
                                    <FontIcon value="event" />
                                </div>
                                <div className="col-xs-2 form-label" data-required={schema.RESOLVED_IN_INTERACTION.required}>
                                    {schema.RESOLVED_IN_INTERACTION.label}
                                </div>
                                <div className="col-xs-4">
                                    <Dropdown disabled={!this.state.assigned}
                                        source={schema.RESOLVED_IN_INTERACTION.options}
                                        value={task.resolution.resolvedInInteraction}
                                        onChange={(value: YesOrNo)=> {task.resolution.resolvedInInteraction = value; this.update();}}/>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.CUSTOMER_CONTACT_SUCCESSFUL.required}>
                                    {schema.CUSTOMER_CONTACT_SUCCESSFUL.label}
                                </div>
                                <div className="col-xs-4">
                                    <Dropdown disabled={!this.state.assigned}
                                        source={schema.CUSTOMER_CONTACT_SUCCESSFUL.options}
                                        value={task.resolution.customerContactSuccessful}
                                        onChange={(value: string)=> {task.resolution.customerContactSuccessful = value; this.update();}}/>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.SLA_OVERALL.required}>
                                    {schema.SLA_OVERALL.label}
                                </div>
                                <div className="col-xs-4">
                                    <Input type='text' disabled={true}
                                        name={schema.SLA_OVERALL.key} 
                                        value={task.complaint.sla.overall} 
                                        required={schema.SLA_OVERALL.required} />
                                </div>
                                <div className="col-xs-2 form-label" data-required={schema.SLA_INTERNAL.required}>
                                    {schema.SLA_INTERNAL.label}
                                </div>
                                <div className="col-xs-4">
                                    <Input type='text' disabled={true}
                                        name={schema.SLA_INTERNAL.key} 
                                        value={task.complaint.sla.internal} 
                                        required={schema.SLA_INTERNAL.required} />
                                </div>
                            </div>
                        </div>
                    </div>
                    : null
                }

                {/* Ticket Info */}
                {
                    task.id? 
                    <div className="customer-info-container">
                        <span className="customer-info-name">Ticket Info</span>
                        <div className="customer-info-content">
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.RESOLVING_DEPARTMENT.required}>
                                    {schema.RESOLVING_DEPARTMENT.label}
                                </div>
                                <div className="col-xs-4">
                                    <Dropdown disabled={true}
                                        source={schema.RESOLVING_DEPARTMENT.options}
                                        value={task.ticket.departament} />
                                </div>
                                <div className="col-xs-2 form-label" data-required={false}>
                                    {"Assignee"}
                                </div>
                                <div className="col-xs-4">
                                    <Input type='text' disabled={true}
                                        name={"Assignee"} 
                                        value={task.original.assignee} />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.CREATED_BY_NAME.required}>
                                    {schema.CREATED_BY_NAME.label}
                                </div>
                                <div className="col-xs-4">
                                    <Input type='text' disabled={true}
                                        name={schema.CREATED_BY_NAME.key} 
                                        value={task.ticket.createdByName} />
                                </div>
                                <div className="col-xs-2 form-label" data-required={false}>
                                    {"Created on"}
                                </div>
                                <div className="col-xs-4">
                                    <Input type='text' disabled={true}
                                        name={"Created on"} 
                                        value={format(task.original.createTime, 'MM/DD/YYYY HH:MM')} 
                                        required={false} />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-xs-2 form-label" data-required={schema.TICKET_SOURCE.required}>
                                    {schema.TICKET_SOURCE.label}
                                </div>
                                <div className="col-xs-4">
                                    <Dropdown disabled={true}
                                        source={schema.RESOLVING_DEPARTMENT.options}
                                        value={task.ticket.source} />
                                </div>
                            </div>
                        </div>
                    </div>
                    : null
                }

                {/* Attachments */}
                <div className="customer-info-container">
                    <span className="customer-info-name">Atachments</span>
                    <div className="customer-info-content">
                        {
                            task.files.map((file: TaskAttachements, index: number)=> (
                                file.uploaded ?
                                <div className="row" key={index}>
                                    <div className="col-xs-12">{file.name} <a href={`${dataService.config.url.boss}api/boss/attachments/${file.id}/actions/export`} target="_blank">{file.description}</a></div>
                                </div>
                                : 
                                <div className="row" key={index}>
                                    <div className="col-xs-1 form-label" data-required={false}>
                                        Name:
                                    </div>
                                    <div className="col-xs-3">
                                        <Input type='text' disabled={!this.state.assigned}
                                            value={file.name} 
                                            onChange={(value: string)=> {file.name = value; this.update();}} />
                                    </div>
                                    <div className="col-xs-3">{file.description || ''}</div>
                                    <div className="col-xs-5">
                                        <label htmlFor={`upload-file-${index}`} className="file-upload-label">
                                            <Button label="Select file" raised primary />
                                        </label>
                                        <input type='file' style={{display: 'none'}} 
                                            id={`upload-file-${index}`} multiple={false}
                                            onChange={(event: any)=> {file.file = event.target.files[0]; file.description = file.file.name; this.update();}} />

                                        <Button label="Remove" raised style={{marginLeft: '20px'}}
                                            onClick={()=> {task.files.splice(index, 1); this.update();}} />
                                    </div>
                                </div>
                            ))
                        }
                        <div className="row" style={{marginTop: '20px'}}>
                            <div className="col-xs-3">
                                <Button label="Attach file" raised primary disabled={!this.state.assigned || task.files.length >= 10}
                                    onClick={()=> {task.files.push({uploaded: false, name: ''}); this.update();}} />
                            </div>
                        </div>
                    </div>
                </div>
                
                {
                    !task.id && this.state.assigned?
                    <div className="customer-info-content">
                    <div className="row">
                        <div className="col-xs-10">&nbsp;</div>
                        <div className="col-xs-2">
                            <Button label="Create Complaint" raised primary 
                                onClick={()=> this.createComplaint()}
                                disabled={this.state.hasErrors || !this.state.modified || !this.state.user.hasRightTo(BOSSUserRightKeys.CREATE_TICKET, task.ticket.departament)} />
                        </div>
                    </div>
                    </div>
                    : null
                }

                {
                    task.id && this.state.assigned?
                    <div className="customer-info-content">
                    <div className="row">
                        <div className="col-xs-6"> &nbsp;
                            { /* <Button label="View History" raised primary /> */ }
                        </div>
                        <div className="col-xs-6" style={{textAlign: 'right'}}>
                            <Button label="Save Changes" raised primary 
                                onClick={()=> this.updateComplaint()}
                                disabled={this.state.hasErrors || !this.state.modified || ! this.state.user.hasRightTo(BOSSUserRightKeys.UPDATE_TICKET, task.ticket.departament)}/>
                            <Button label="Resolve" raised primary style={{marginLeft: '20px'}} 
                                onClick={()=> this.resolveComplaint()}
                                disabled={this.state.hasErrors || this.state.modified || !this.state.user.hasRightTo(BOSSUserRightKeys.CLOSE_TICKET, task.ticket.departament)} />
                        </div>
                    </div>
                    </div>
                    : null
                }
            {    
                this.state.modals.assignTask && this.state.user.hasRightTo(BOSSUserRightKeys.ASSIGN_TICKET, task.ticket.departament)?
                <Dialog className="xr-modal" actions={this.state.modals.assignTask.actions} active={true}
                    title='Assign Ticket'>
                    <section className="customer-info-content">
                        <div className="row">
                            <div className="col-xs-4">Please select a name from the list</div>
                            <div className="col-xs-6">
                                <Dropdown 
                                        source={this.state.modals.assignTask.users.map(function(user: BOSSUsers){
                                            return {label: `${user.firstName} ${user.lastName}`, value: user.id};
                                        })}
                                        onChange={(user: string)=> {
                                            var {assignTask} = this.state.modals;
                                            assignTask.user = user;
                                            this.setState({modals: {assignTask}})
                                        }}
                                        value={this.state.modals.assignTask.user} />
                            </div>
                        </div>
                    </section>
                </Dialog>
                : null
            }
            {    
                this.state.modals.changeDepartment && this.state.user.hasRightTo(BOSSUserRightKeys.CHANGE_DEPARTMENT, task.ticket.departament)?
                <Dialog className="xr-modal" actions={this.state.modals.changeDepartment.actions} active={true}
                    title='Change Ticket to Department'>
                    <section className="customer-info-content">
                        <div className="row">
                            <div className="col-xs-4">Please select a department from the list</div>
                            <div className="col-xs-6">
                                <Dropdown 
                                        source={this.state.modals.changeDepartment.departments}
                                        onChange={(department: string)=> {
                                            var {changeDepartment} = this.state.modals;
                                            changeDepartment.department = department;
                                            this.setState({modals: {changeDepartment}})
                                        }}
                                        value={this.state.modals.changeDepartment.department} />
                            </div>
                        </div>
                    </section>
                </Dialog>
                : null
            }
			</div>
		);
	}
}

export const NewTicket = injectIntl(NewTicketWrapped);



