require("babel-polyfill");

import * as React from 'react';
import * as ReactDOM from 'react-dom';

import { Button } from "react-toolbox/lib/button";
import { Snackbar } from 'react-toolbox/lib/snackbar';
import Navigation from 'react-toolbox/lib/navigation';
import { Dropdown } from 'react-toolbox/lib/dropdown';
import { FontIcon } from 'react-toolbox/lib/font_icon';
import { HashRouter as Router, Route, Link } from 'react-router-dom';
import { IntlProvider, FormattedMessage, addLocaleData, Locale, InjectedIntlProps, injectIntl } from 'react-intl';

import { HomeComponent } from "./components/home.component";
import { NewTicket } from "./components/new-ticket.component";
import { QueueComponent } from "./components/queue.component";

import { BossCn, BOSSLanguages, BOSSUser, TaskSchemaDefinition, Tasks, TaskSchemaProperty, TaskSchemaPropertyOptions} from './models';

import axios from 'axios';
import { AxiosResponse, AxiosError, AxiosInstance } from 'axios';

import { dataService } from './data-service';
import { logger } from './logger';

import { TestRunner } from './test-api';

import * as PubSub from 'pubsub-js';

const initialState: BossCn = {

};

logger.group('Application initialization');

var messages: Map<string, string> = {} as Map<string, string>;

interface AppComponentState {
	init?: {
		error?: string;
		unauthorized?: boolean;
	}
	messages: Map<string, string>;
	locale?: string;
	languages: Array<Object>;
	user?: BOSSUser;
	queueItem: any;
}

class AppComponent extends React.Component<{}, AppComponentState> {

	state: AppComponentState = {
		init: {},
		locale: 'en',
		languages: [],
		messages: null,
		user: null,
		queueItem: 'My Queues'
	}

	componentDidMount()	{
		logger.log('Loading configuration and user rights.')
		axios.all([axios.get('config'), axios.get('rights')]).then(axios.spread((configResponse: AxiosResponse, rightsResponse: AxiosResponse) => {
			dataService.setConfig(configResponse.data);
			logger.log('Loaded configuration', dataService.config);
			dataService.setUserRights(rightsResponse.data);
			logger.log('Loaded user rights', dataService.rights);
			this.loadData();	
		})).catch((error: Error) => {
			logger.error(error);
			this.setState({init: {error: 'message.internalServerError'}});
			logger.ungroup();
		});
	}

	loadData() {
		logger.group('Application data');
		dataService.loadLanguages().then((languages: Array<BOSSLanguages>) => {
			var localeLanguages: any[] = languages.map((language: BOSSLanguages) => {
				return {
					value: language.id,
					label: language.description
				};
			});
			logger.log('Languages', localeLanguages);
			var locale: string;
			if (localeLanguages.length) {
				locale = window.localStorage.getItem('boss.us.cp.locale') || localeLanguages[0].value || 'en';
			}
			this.setState({languages: localeLanguages, locale});
			logger.log('Current language: ', locale);
			return dataService.loadLanguage(locale);
		}).then((messages: any) => {
			logger.log('Current language dictionary:', messages);
			addLocaleData({
				locale: this.state.locale,
				fields: messages,
				pluralRuleFunction: function(n: number, ord: boolean): string {return '';}
			} as Locale);
			this.setState({messages});

			return dataService.loadSchemaDefinition();
		}).then(function(schema: TaskSchemaDefinition){
			logger.log('Task schema definition:', schema);
			return dataService.loadUser();
		}).then((user: BOSSUser) => {
			logger.log('Current user: ', user);
			this.setState({user: user});
			logger.ungroup(); // Application data
			logger.ungroup(); // Application initialization
		}).catch((error: Error) => {
			logger.error(error);
			this.setState({init: {error: 'message.notAuthorized', unauthorized: true}});
			logger.ungroup(); // Application data
			logger.ungroup(); // Application initialization
			setTimeout(() => {
				dataService.logout();
			}, 3000);
		});
	}

	changeLanguage(locale: string) {
		logger.group(`Changin locale <${locale}>`);
		dataService.loadLanguage(locale).then((messages: any) => {
			logger.log('Locale dictionary: ', messages);
			addLocaleData({
				locale: locale,
				fields: messages
			} as Locale);
			window.localStorage.setItem('boss.cn.dv.locale', locale);
			this.setState({messages, locale: locale});
			logger.ungroup();
		}).catch(function(error: Error){
			logger.error(error);
			PubSub.publish('ERROR', error);
			logger.ungroup();
		});
	}

	render(){
		if (!this.state.messages) {
			return <div></div>;
		}
		if (!this.state.user) {
			return (
				<IntlProvider key={this.state.locale} locale={this.state.locale} messages={this.state.messages}>
					<div className="app-vc-parent app-loading-screen">
						<div className="app-vc-child">
							<div className="app-loading-screen__logo">
								<img src={require('../images/logo.png')} />
							</div>
							{
								this.state.init.error ?
								<div className="app-loading-screen__message app-loading-screen__error">
									<div><FormattedMessage id={this.state.init.error} defaultMessage="Internal Application Error" /></div>
									{
										!this.state.init.unauthorized ?
										<div>
											<FormattedMessage id="message.internalServerErrorDescription" defaultMessage="The application could not be initialized properly." />
										</div>
										: null
									}
								</div>
								: 
								<div className="app-loading-screen__message">
									<FormattedMessage id="boss.custom.fe.message.loading" defaultMessage="Loading..." />
								</div>
							}
						</div>
					</div>
				</IntlProvider>
			);
		}
		
		var user: BOSSUser = dataService.getUser();
		var listTicketDepartments: string[] = user.rights.LIST_TICKET || [];
		var departments: TaskSchemaPropertyOptions[] = [];
		departments = dataService.schema.RESOLVING_DEPARTMENT.options.filter((option: TaskSchemaPropertyOptions)=> {
			return option.key.indexOf(`level.1.`) > -1 && listTicketDepartments.indexOf(option.value) > -1;
		});

		return (
			<IntlProvider key={this.state.locale} locale={this.state.locale} messages={this.state.messages}>
				<Router>
					<div className="app">
						<HeaderComponent user={this.state.user} locale={this.state.locale} languages={this.state.languages}
							onLanguageChange={(locale: string)=> this.changeLanguage(locale)} />
						<div className="row app-navbar">
							<div className="col-xs-12 app-header__navigation">
								<div>
								<ul className="app-header__navitems">
									<li className="headerItem"><Link to='/'><FormattedMessage id="NON EXISTING ID" defaultMessage="My Tickets" /> </Link></li>
									<li className="headerItem"><Link to='/complaint'><FormattedMessage id="NON EXISTING ID" defaultMessage="Create New Ticket" /> </Link></li>
									{
										departments.map((option: TaskSchemaPropertyOptions)=>(
											<li className="headerItem" key={option.value}>
												<Link to={`/queue/${option.value}`}><FormattedMessage id={option.key} defaultMessage={option.value} /></Link>
											</li>
										))
									}
								</ul>
								</div>
							</div>
						</div>
						<Route exact path='/' component={HomeComponent} />
						<Route path='/complaint/:pid?/:id?' component={NewTicket} />							
						<Route path='/queue/:department' component={QueueComponent} />							
					</div>
				</Router>
			</IntlProvider>
		);
	}
}


interface HeaderProps {
	user: BOSSUser;

	locale: string;
	languages: Array<Object>;

	onLanguageChange: (locale: string)=> void;
}


interface HeaderState {
	errorMessageDisplayed: boolean;
	snackBarText: string;
	snackBarClass: string;
}

class HeaderComponentWrapped extends React.Component<HeaderProps & InjectedIntlProps, HeaderState> {

	unsubscribeTopic: void;
	unsubscribeInfo: void;

	state: HeaderState = {
		errorMessageDisplayed: false,
		snackBarText: '',
		snackBarClass: ''
	}

	componentDidMount() {
		var errorTimeout: number = -1;
		let handleMessages = (msg: any, messageClass: string): void => {
			if (errorTimeout > -1) {
				window.clearTimeout(errorTimeout);
			}
			errorTimeout = window.setTimeout(()=> {
				this.hideMessage();
				errorTimeout = -1;
			}, 5000);
			this.showMessage(msg, messageClass);
		}

		var schema: TaskSchemaDefinition = dataService.schema;
        Object.keys(schema).forEach((key: keyof TaskSchemaDefinition) => {
            var property: TaskSchemaProperty = schema[key];
            if (property.options && property.options.length) {
                property.options.forEach((option: TaskSchemaPropertyOptions)=> {
                    option.label = this.props.intl.formatMessage({id: option.key});
                });
            }
        });

		this.unsubscribeTopic = PubSub.subscribe('ERROR', (topic: string, error: any) => {
			var message: string = this.props.intl.formatMessage({id: 'message.internalServerError'});
			if (typeof error.message == 'string') {
				message = this.props.intl.formatMessage({id: error.message});
			} else if (error.message instanceof Array) {
				var messages: string[] = error.message;
				message = messages.map((m: string)=> this.props.intl.formatMessage({id: m})).join(' ');
			}
			handleMessages(message, 'snackbar-error');
		});

		this.unsubscribeInfo = PubSub.subscribe('INFO', (topic: string, msg: any) => {
			handleMessages(this.props.intl.formatMessage({id: msg}), 'snackbar-approve');
		});
	}


	componentWillUnmount() {
		PubSub.unsubscribe(this.unsubscribeTopic);
		PubSub.unsubscribe(this.unsubscribeInfo);
	}

	showMessage(error: any, messageClass: string) {
		this.setState({errorMessageDisplayed: true, snackBarText: error, snackBarClass: messageClass});
	}

	hideMessage() {
		this.setState({errorMessageDisplayed: false});
	}

	logout() {
		dataService.logout();
	}

	render() {
		return (
			<div className="row app-header">
				<div className="col-xs-7">
					<img className="app-content__logo" src={require('../images/logo.png')} />
				</div>
				<div className="col-xs-5">
					<div className="app-header__actions">
						{/* <FontIcon className="app-header__actions__translate" value="translate"/>
						<Dropdown
							auto
							onChange={(value: string) => {this.props.onLanguageChange ? this.props.onLanguageChange(value) : null;}}
							source={this.props.languages}
							value={this.props.locale}
							className="app-header__actions__language"
						/> */}
						<Button icon="person" label={this.props.user.id}/>
						<Button icon="power_settings_new" onClick={()=> this.logout()} />
					</div>
				</div>
				<Snackbar
					active={this.state.errorMessageDisplayed}
					label={this.state.snackBarText}
					className={'snack-bar ' + this.state.snackBarClass}
				/>
			</div>
		);
	}

}

const HeaderComponent = injectIntl(HeaderComponentWrapped);



(function(){
	ReactDOM.render(<AppComponent />, document.getElementById("app"));
})();



